import React, { useContext } from "react";
import "./LoginRegister.css";
import { Link, withRouter } from "react-router-dom";
import FormBody from "./LoginRegisterFormBody";
import { UserAuthContext } from "../../Contexts/UserAuthContext";
import Error_404 from "../Error_404";
import ErrorSuccessInfoModal from "../ErrorSuccessInfoModal/ErrorSuccessInfoModal";
//import success from "../../images/success.gif";
//import { ErrorSuccessInfoContext } from "../../Contexts/ErrorSuccessInfoModalContext";
import { AppThemeContext } from "../../Contexts/AppThemeContext";

function Register(props) {
	const { storeUserInfo, isUserLoggedIn, checkRegisterUserInput } = useContext(
		UserAuthContext
	);
	const { name, email } = useContext(UserAuthContext);
	//const { selectedTheme } = useContext(AppThemeContext);
	const actionOnRegisterBtnClick = () => {
		if (checkRegisterUserInput()) {
			storeUserInfo();
			if (navigator.onLine) {
				fetch(
					"https://v1.nocodeapi.com/vaibhavwf/google_sheets/CQNjyulSXhrWTVuW?tabId=Sheet1",
					{
						method: "POST",
						headers: {
							"Content-Type": "application/json",
						},
						body: JSON.stringify([[name, email]]),
					}
				)
					.then()
					.catch();
			}
			//console.log(name, email);
		}
	};
	return isUserLoggedIn ? (
		<Error_404 />
	) : (
		<div className="login-register-container d-flex  justify-content-center align-items-center flex-column">
			<Link
				to="/todo-money"
				className="navigate-route-arrow-login-register  px-2 py-1 rounded ml-3 d-flex justify-content-center align-items-center"
				style={{
					color: "var(--green)",
				}}
			>
				<span>&larr;</span>
			</Link>
			<div className="register-container mx-auto p-3">
				<h1 className="user-auth-form-header">Create New Account</h1>
				<FormBody />
				<ErrorSuccessInfoModal
					link="/user-auth/login"
					linkText="Login Here"
					modalImage="https://raw.githubusercontent.com/vaibhavkaul69/todo-money/master/src/images/success.gif"
					modalText="Account Created Successfully.!<br/> Please Login to Continue"
				/>
				<div className="user-auth-form-footer d-flex justify-content-around align-items-center">
					<div className="d-flex justify-content-center align-items-center flex-column"></div>
					<button
						id="userRegister"
						className="text-white w-100"
						onClick={actionOnRegisterBtnClick}
					>
						Register
					</button>
				</div>
			</div>
		</div>
	);
}
export default withRouter(Register);



