import React, { useContext, useEffect } from "react";
import { AppThemeContext } from "../Contexts/AppThemeContext";
//import { Link } from "react-router-dom";
import Footer from "./Footer/Footer";
//CSS in App.css
/*
 Custom Modules exported here
 */
import TransactionHomepageEntities from "./TransactionHomepageEntities";
//import RecentActivityContainer from "./RecentActivity/RecentActivityContainer";
//import FeedbackForm from "./Feedback/FeedbackForm";

function TodoHome() {
	useEffect(() => {
		document.title = "TODO Money - Home ";
	}, []);
	const { theme, selectedTheme } = useContext(AppThemeContext);
	return (
		<React.Fragment>
			<div className="main-bg-container d-flex flex-column  justify-content-center align-items-center">
				{/*
			<RecentActivityContainer />
			*/}
				<div className="container todo-home-container">
					<TransactionHomepageEntities />
					<h1
						className="todo-home-head mt-5 text-center"
						style={{
							color: selectedTheme == "light" ? "var(--blue)" : "var(--green)",
						}}
					>
						A few important points that you should keep in mind while using Todo
						Money
					</h1>
					<ul className="todo-home-info-points text-white">
						<li
							style={{
								background: "var(--red)",
							}}
						>
							All of your data is stored within your{" "}
							<strong>Web Browser</strong>.
						</li>
						<li
							className="font-weight-bold"
							style={{
								background: "var(--red)",
							}}
						>
							Do Not Clear Your Browsers data or Uninstall The Browser as that
							will Erase all the data that you have saved in this App including
							Users Transaction and Login Information.
						</li>
						<li
							style={{
								background: "var(--red)",
							}}
						>
							This App can be used in all browsers but it works best with{" "}
							<strong> Google Chrome</strong>.
						</li>
					</ul>
				</div>
			</div>

			<Footer />
		</React.Fragment>
	);
}

export default TodoHome;



