import React, { useContext } from "react";
import { Link, withRouter } from "react-router-dom";
import { UserAuthContext } from "../../Contexts/UserAuthContext";
import "./LoginRegister.css";
import Error_404 from "../Error_404";
import ErrorSuccessInfoModal from "../ErrorSuccessInfoModal/ErrorSuccessInfoModal";
import FormBody from "./LoginRegisterFormBody";
//import failed from "../../images/failed.gif";
import { AppThemeContext } from "../../Contexts/AppThemeContext";

function Login(props) {
	const { changeUserAuthLoginStatus, isUserLoggedIn } = useContext(
		UserAuthContext
	);
	//const { selectedTheme } = useContext(AppThemeContext);
	const userLoginActivity = () => {
		if (changeUserAuthLoginStatus()) {
			props.history.push("/todo-home");
		}
	};
	return isUserLoggedIn ? (
		<Error_404 />
	) : (
		<div className="login-register-container d-flex justify-content-center align-items-center flex-column">
			<Link
				to="/todo-money"
				className="navigate-route-arrow-login-register  px-2 py-1 rounded ml-3 d-flex justify-content-center align-items-center"
				style={{
					color: "var(--green)",
				}}
			>
				&larr;
			</Link>
			<div className="login-container mx-auto p-3">
				<h1 className="user-auth-form-header">Login Now</h1>
				<FormBody />
				<ErrorSuccessInfoModal
					modalImage="https://raw.githubusercontent.com/vaibhavkaul69/todo-money/master/src/images/failed.gif"
					modalText="You need to create an account first <br/>- Or -<br/> You have written wrong credentials.!"
				/>
				<div className="user-auth-form-footer d-flex justify-content-around align-items-center">
					<button
						id="userLogin"
						className="text-white w-100"
						onClick={() => userLoginActivity()}
					>
						Login
					</button>
				</div>
			</div>
		</div>
	);
}
export default withRouter(Login);



