import React, { useContext } from "react";
import { InputValidateContext } from "../../Contexts/InputValidationContext";
import { UserTransactionDataContext } from "../../Contexts/UserTransactionDataContext";
import InputErrorMessage from "../InputErrorMessage/InputErrorMessage";

function AddTransactionModal_Body(props) {
	const { visibleFormBody, setVisibleFormBody } = props;
	const {
		name,
		description,
		amount,
		dateOfTransaction,
		phone,
		setName,
		setDescription,
		setPhone,
		setAmount,
		setDateOfTransaction,
		userTransactionsInfo,
		changeUserAddHistory,
	} = useContext(UserTransactionDataContext);

	const { checkInputError } = useContext(InputValidateContext);

	function actionOnInputModalChange(e) {
		if (e.target.name == "name") {
			setName(e);
		} else if (e.target.name == "select-name") {
			console.log(e.target.value);
			changeUserAddHistory(e.target.value);
		} else if (e.target.name == "amount") {
			setAmount(e);
		} else if (e.target.name == "date") {
			setDateOfTransaction(e);
		} else if (e.target.name == "description") {
			setDescription(e);
		} else {
			setPhone(e);
		}
		checkInputError(e.target);
	}
	return (
		<div className="modal-container__modal-body p-3">
			{visibleFormBody == "addUser" ? (
				<div className="user-main-form-body mt-3" id="addUser_Body">
					<h1 className="modal-form-label m-0">
						New Username<span className="important-asterik">*</span>:
					</h1>
					<p className="tutorial-text mb-2">
						Add a new user here. Please make sure the name{" "}
						<strong>does not exceeds 10 characters</strong> else it will be
						trimmed.
					</p>
					<input
						className="form-input-field p-2"
						onChange={(e) => actionOnInputModalChange(e)}
						type="search"
						name="name"
						value={name}
						required
						placeholder="Name"
					/>
					<InputErrorMessage />
					<h1 className="modal-form-label mt-3 mb-0">
						Phone<span className="important-asterik">*</span>:
					</h1>
					<div className="tutorial-text my-2">
						This number will be used to notify the user about the transaction
						amount. Do not add <strong>0</strong> or any code like{" "}
						<strong>+91 </strong> before your number. <br /> You will get :
						<ul className="phone-notify-points my-2">
							<strong>
								<li>1 Free Text Message Notifications </li>
							</strong>
							<strong>
								<li>Unlimited Whatsapp Message Notification </li>
							</strong>
						</ul>
						<strong>
							Text messages will only be sent between 9 AM - 9 PM. If you try
							sending text notifications after 9 PM then they will be scheduled
							at 9 AM next morning :)
						</strong>
					</div>
					<input
						className="form-input-field p-2"
						onChange={(e) => actionOnInputModalChange(e)}
						type="number"
						name="phone"
						value={phone}
						required
						placeholder="Phone"
					/>
					<InputErrorMessage />
					{/*
					<h1 className="modal-form-label mt-3">Date:</h1>
					<p className="tutorial-text mb-2 text-left">
						Date on which this User is added.
					</p>
					
					<input
						className="form-input-field p-2 w-50 "
						type="date"
						required
						onChange={(e) => setDateOfUserAdded(e)}
						placeholder="Date of Transaction"
					/>
					<InputErrorMessage/>
					*/}
				</div>
			) : (
				<div className="user-history-form-body mt-3" id="addUserHistory_Body">
					<h1 className="modal-form-label ">
						Select Username<span className="important-asterik">*</span>:
					</h1>
					<p className="tutorial-text mb-2 text-left">
						Select the username from the list below. The following transaction
						will be added as Transaction-History for the selected user.
					</p>
					<select
						required
						value={name}
						name="select-name"
						className="form-input-field p-2 user-name-dropdown-modal"
						onChange={(e) => actionOnInputModalChange(e)}
					>
						<option defaultValue={null} value="null">
							Choose a name
						</option>
						{userTransactionsInfo.map((item) => {
							return (
								<option key={item.id} value={item.name}>
									{item.name}
								</option>
							);
						})}
					</select>
					<InputErrorMessage />
					<h1 className="modal-form-label mt-3">
						Amount<span className="important-asterik">*</span>:
					</h1>
					<input
						className="form-input-field p-2 w-50 "
						type="number"
						required
						value={amount}
						name="amount"
						onChange={(e) => actionOnInputModalChange(e)}
						placeholder="Amount"
					/>
					<p className="tutorial-text my-2 text-left">
						Amount greater than <strong>99,99,99,9999 </strong>will be trimmed
						to 10 digits.
					</p>
					<InputErrorMessage />
					<h1 className="modal-form-label mt-2">
						Date of Transaction<span className="important-asterik">*</span>:
					</h1>
					<input
						className="form-input-field p-2 w-50 "
						type="date"
						required
						name="date"
						value={dateOfTransaction}
						onChange={(e) => actionOnInputModalChange(e)}
						placeholder="Date of Transaction"
					/>
					<InputErrorMessage />
					<h1 className="modal-form-label mt-3">
						Description<span className="important-asterik">*</span>:
					</h1>
					<textarea
						className="form-input-field h-25 p-2"
						type="number"
						required
						value={description}
						name="description"
						onChange={(e) => actionOnInputModalChange(e)}
						placeholder="Transaction Description"
					></textarea>
					<InputErrorMessage />
				</div>
			)}
		</div>
	);
}

export default AddTransactionModal_Body;



