import React, { useContext } from "react";
import { Link } from "react-router-dom";
//CSS in App.css
import { AppThemeContext } from "../Contexts/AppThemeContext";
//import noTransactionImage from "../images/no-transaction.png";

function NoTransactionEntry() {
	const { selectedTheme } = useContext(AppThemeContext);
	return (
		<div className="no-transaction-entry-container d-flex flex-column justify-content-center align-items-center">
			<img
				src="https://raw.githubusercontent.com/vaibhavkaul69/todo-money/master/src/images/no-transaction.png"
				className="img-fluid"
				alt="No transactions made in image"
			/>
			<h1
				className="no-transaction-entry-text text-center"
				style={{ color: selectedTheme == "light" ? "#1a1a1a" : "#fff" }}
			>
				Oops....! <br />
				Looks like you don't have <br /> any transactions.
			</h1>
			{window.location.hash == "#/money-given" ? null : (
				<Link
					to="/money-given"
					className="no-transaction-entry-cta-btn text-white d-flex mt-4 justify-content-center align-items-center p-3"
					style={{
						background:
							selectedTheme == "light" ? "var(--blue)" : "var(--green)",
					}}
				>
					Go to Money Given to add one now
				</Link>
			)}
		</div>
	);
}

export default NoTransactionEntry;



