import React, { useContext, useState } from "react";
//import feedbackImage from "../../images/feedback.svg";
import "./FeedbackForm.css";
import { AppThemeContext } from "../../Contexts/AppThemeContext";
import Footer from "../Footer/Footer";
import AccessDenied from "../AccessDenied";
import ErrorSuccessInfoModal from "../ErrorSuccessInfoModal/ErrorSuccessInfoModal";
import { ErrorSuccessInfoContext } from "../../Contexts/ErrorSuccessInfoModalContext";
import { UserAuthContext } from "../../Contexts/UserAuthContext";

function FeedbackForm() {
	const { selectedTheme } = useContext(AppThemeContext);
	const { changeErrorSuccessInfoModalState } = useContext(
		ErrorSuccessInfoContext
	);
	const { isUserLoggedIn } = useContext(UserAuthContext);
	const [name, setName] = useState("");
	const [email, setEmail] = useState("");
	const [feedback, setFeedback] = useState("");

	const classBasedOnTheme = {
		light: "light-theme-input text-dark",
		dark: "dark-theme-input text-dark",
	};
	function checkFeedbackFormInputs() {
		//console.log("Feedback check");
		const lowerEmail = email.toLowerCase();

		const emailRegex = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
		if (
			name.length > 2 &&
			/[a-zA-Z0-9]+/.test(name) &&
			/[a-zA-Z0-9]+/.test(lowerEmail) &&
			emailRegex.test(lowerEmail) &&
			feedback.length > 5 &&
			/[a-zA-Z0-9]+/.test(feedback)
		) {
			return true;
		} else {
			return false;
		}
	}
	function submitFeedbackForm() {
		if (checkFeedbackFormInputs()) {
			if (window.navigator.onLine) {
				fetch(
					"https://v1.nocodeapi.com/spartapro/google_sheets/dGOjbnQpCdGxLqaT?tabId=Sheet1",
					{
						method: "POST",
						headers: {
							"Content-Type": "application/json",
						},
						body: JSON.stringify([[name, email, feedback]]),
					}
				)
					.then((res) => {
						changeErrorSuccessInfoModalState();
						setName("");
						setEmail("");
						setFeedback("");
						return res.json();
					})
					.then()
					.catch((err) => {
						alert(
							"Feedback could not be submitted :(\n Either an error has occured or you are offline.\n Please try again :)"
						);
					});
				alert("Sending feedback.....");
			} else {
				alert(
					"You are offline.!\nYou need to be online to submit a feedback :)"
				);
			}
		} else {
			alert("Please enter some valid values.!");
		}
	}
	return isUserLoggedIn ? (
		<React.Fragment>
			<div className="main-bg-container">
				<section className="feedback-form-footer py-2">
					<div className="container">
						<h1
							className=" p-2 feedback-form-heading text-white text-center mb-3"
							style={{
								background:
									selectedTheme == "light" ? "var(--blue)" : "var(--green)",
							}}
						>
							Feedback Form
						</h1>
						<h3
							className="feedback-form-subtext text-center"
							style={{
								color:
									selectedTheme == "light" ? "var(--blue)" : "var(--green)",
							}}
						>
							Please provide your valuable feedback about what more features you
							want to be added, what all features you liked in the app, what are
							the areas of improvement and any feedback in general.
							<br />
							<br /> <strong>Thank You. !</strong>
						</h3>
						<div className="row w-100 h-100 mt-5 mx-auto">
							<div className="col-xl-6 col-lg-6 col-md-6 col-12 d-flex justify-content-center align-items-center">
								<img
									src="https://raw.githubusercontent.com/vaibhavkaul69/todo-money/9eb84ca031353c5c690a07195fc3b0da86dac183/src/images/feedback.svg"
									alt="Feedback Icon"
									className="feedback-icon "
								/>
							</div>
							<div className="col-xl-6 col-lg-6 col-md-6 col-12 pl-xl-5 pl-lg-5 pl-md-5 pl-2 d-flex flex-column justify-content-center align-items-center">
								<div className="feedback-form w-100">
									<h2
										className="feedback-form-label name-label"
										style={{
											color:
												selectedTheme == "light"
													? "var(--blue)"
													: "var(--green)",
										}}
									>
										Name:
									</h2>
									<input
										type="search"
										name="name"
										value={name}
										onChange={(e) => setName(e.target.value)}
										placeholder="Enter your name"
										className={"mt-1 p-2 " + classBasedOnTheme[selectedTheme]}
									/>
									<h2
										className="feedback-form-label mt-3 email-label"
										style={{
											color:
												selectedTheme == "light"
													? "var(--blue)"
													: "var(--green)",
										}}
									>
										Email:
									</h2>
									<input
										type="email"
										name="email"
										value={email}
										onChange={(e) => setEmail(e.target.value)}
										placeholder="Enter your email"
										className={"mt-1 p-2 " + classBasedOnTheme[selectedTheme]}
									/>
									<h2
										className="feedback-form-label mt-3 feedback-label"
										style={{
											color:
												selectedTheme == "light"
													? "var(--blue)"
													: "var(--green)",
										}}
									>
										Feedback:
									</h2>
									<textarea
										type="search"
										name="feedback"
										value={feedback}
										onChange={(e) => setFeedback(e.target.value)}
										placeholder="Provide your valuable feedback here"
										className={
											"mt-1 mb-2 p-2 feedback-input " +
											classBasedOnTheme[selectedTheme]
										}
									></textarea>
									<input
										type="submit"
										value="Submit"
										onClick={submitFeedbackForm}
										className="submit-feedback-form-btn mt-2 text-white"
										style={{
											background:
												selectedTheme == "light"
													? "var(--blue)"
													: "var(--green)",
										}}
									/>
								</div>
							</div>
						</div>
					</div>
				</section>
				{}
				<ErrorSuccessInfoModal
					modalImage="https://raw.githubusercontent.com/vaibhavkaul69/todo-money/master/src/images/success.gif"
					modalText="Thanks for your feedback"
				/>
			</div>
			<Footer />
		</React.Fragment>
	) : (
		<AccessDenied />
	);
}

export default FeedbackForm;



