import React, { useContext, useState } from "react";
//import { InputValidateContext } from "../../Contexts/InputValidationContext";
import { UserAuthContext } from "../../Contexts/UserAuthContext";
import InputErrorMessage from "../InputErrorMessage/InputErrorMessage";
import "./LoginRegister.css";

function LoginRegisterFormBody() {
	const [showPassword, setShowPassword] = useState(false);
	const {
		setUserAuthName,
		setUserAuthPassword,
		name,
		password,
		email,
		setAuthEmail,
	} = useContext(UserAuthContext);
	//const { checkInputError } = useContext(InputValidateContext);
	//console.log(window.location.hash);
	return (
		<div className="login-register-form-body-container my-3">
			<div className="user-auth-form-body">
				<h2 className="login-register-form-label">Name:</h2>
				<input
					type="search"
					placeholder="Enter name"
					name="uname"
					value={name}
					onChange={(e) => setUserAuthName(e.target.value)}
					className=" p-2"
				/>
				<InputErrorMessage />
				{window.location.hash == "#/user-auth/register" ? (
					<div>
						<h2 className="login-register-form-label">Email:</h2>
						<input
							type="email"
							placeholder="Enter email"
							name="uemail"
							value={email}
							onChange={(e) => setAuthEmail(e.target.value)}
							className=" p-2"
						/>
						<InputErrorMessage />
					</div>
				) : null}
				<h2 className="login-register-form-label">Password:</h2>
				<div className="user-auth-password">
					<input
						type={showPassword ? "text" : "password"}
						placeholder="Enter password"
						name="upass"
						value={password}
						onChange={(e) => setUserAuthPassword(e.target.value)}
						className=" p-2 w-100 password-input"
					/>
					{showPassword ? (
						<img
							src="https://raw.githubusercontent.com/vaibhavkaul69/todo-money/b29543e7548cc548dcb96efa824e45b028cdd934/src/images/hide-pass.svg"
							className="password-toggle-eye"
							onClick={() => setShowPassword(false)}
							alt="Hide PAssword"
						/>
					) : (
						<img
							src="https://raw.githubusercontent.com/vaibhavkaul69/todo-money/b29543e7548cc548dcb96efa824e45b028cdd934/src/images/show-pass.svg"
							className="password-toggle-eye"
							onClick={() => setShowPassword(true)}
							alt="Show PAssword"
						/>
					)}
				</div>
				<InputErrorMessage />
			</div>
		</div>
	);
}
export default LoginRegisterFormBody;



