import React, { useContext } from "react";
import "./Footer.css";
import { AppThemeContext } from "../../Contexts/AppThemeContext";

function Footer() {
	const { selectedTheme } = useContext(AppThemeContext);
	return (
		<footer
			className="app-main-footer w-100 py-3 mt-5"
			style={{
				background:
					selectedTheme == "light"
						? "linear-gradient(to right bottom, var(--green-boxShadow),var(--blue-boxShadow))"
						: "#1a1a1a",
			}}
		>
			<div
				className="container-fluid  d-flex flex-column justify-content-center align-items-center"
				style={{ color: selectedTheme == "light" ? "var(--blue)" : "#fff" }}
			>
				<h1 className="footer-text text-center m-0 font-weight-bold">
					This app is made by Vaibhav Kaul
				</h1>
				<h3 className="follow-footer-text text-center m-0">Follow me on</h3>
				<div className="footer-social-icons">
					<a
						href="https://www.instagram.com/vaibhaaaaavv/"
						target="_blank"
						rel="noreferrer"
					>
						<img
							src="https://raw.githubusercontent.com/vaibhavkaul69/todo-money/4bd63ab8b5a0dfc513ca7f33f4b78c1c675543f3/src/images/instagram.svg"
							alt="Vaibhav Instagram"
							className="social-media-footer-icon"
						/>
					</a>
					<a
						href="https://www.facebook.com/vaibhav.kaul2"
						target="_blank"
						rel="noreferrer"
					>
						<img
							src="https://raw.githubusercontent.com/vaibhavkaul69/todo-money/4bd63ab8b5a0dfc513ca7f33f4b78c1c675543f3/src/images/facebook.svg"
							alt="Vaibhav Facebook"
							className="social-media-footer-icon facebook-footer-icon"
						/>
					</a>
					<a
						href="https://www.linkedin.com/in/vaibhav-kaul-ab5941182/"
						target="_blank"
						rel="noreferrer"
					>
						<img
							src="https://raw.githubusercontent.com/vaibhavkaul69/todo-money/4bd63ab8b5a0dfc513ca7f33f4b78c1c675543f3/src/images/linkedin.svg"
							alt="Vaibhav Linkedin"
							className="social-media-footer-icon"
						/>
					</a>
					<a
						href="https://github.com/vaibhavkaul69"
						target="_blank"
						rel="noreferrer"
					>
						<img
							src="https://raw.githubusercontent.com/vaibhavkaul69/todo-money/4bd63ab8b5a0dfc513ca7f33f4b78c1c675543f3/src/images/github.svg"
							alt="Vaibhav Github"
							className="social-media-footer-icon github-footer-icon"
						/>
					</a>
					<a
						href="mailto:vaibhav.kaul2@gmail.com"
						target="_blank"
						rel="noreferrer"
					>
						<img
							src="https://raw.githubusercontent.com/vaibhavkaul69/todo-money/2d01241c8b432b49c002da635d635ac3078e5690/src/images/gmail.svg"
							alt="Vaibhav Gmail"
							className="social-media-footer-icon"
						/>
					</a>
				</div>
			</div>
		</footer>
	);
}

export default Footer;



