import React, { useContext, useEffect, useState } from "react";
import "../../Components/UserTransactionDataThumbnail.css";
import { AppThemeContext } from "../../Contexts/AppThemeContext";
import clockIcon from "../../images/clock.png";
import userImage from "../../images/user_image.png";
import { withRouter, Link } from "react-router-dom";
import telephone from "../../images/telephone.svg";
//import whatsapp from "../../images/whatsapp.svg";

import { UserTransactionDataContext } from "../../Contexts/UserTransactionDataContext";
import ErrorSuccessInfoModal from "../ErrorSuccessInfoModal/ErrorSuccessInfoModal";

function UserTransactionDataThumbnail_Main(props) {
	const [totalPayableAmount, setTotalPayableAmount] = useState(0);
	const [allTransactionDates, setAllTransactionDates] = useState([""]);
	const {
		name,
		totalAmount,
		id,
		settlementDate,
		phone,
		notifyUserCount,
		transactionHistory,
	} = props;
	//console.log(id, parentId);
	const {
		notifyUserTransaction,
		deleteCurrentTransactionThread,
		allTransactionArrayLength,
		settleCurrentTransaction,
	} = useContext(UserTransactionDataContext);
	const { theme, selectedTheme } = useContext(AppThemeContext);
	let count = 0;
	let dateArray = [];
	let uniqueDateArray = [];
	const addTagLineSpecificToURL = {
		"#/money-given": {
			tagline: `You have to take RS ${totalPayableAmount} from ${name}`,
			amountSummary: `This is the total amount of money you have given to ${name}`,
		},
	};

	useEffect(() => {
		transactionHistory
			? transactionHistory.map((item) => {
					count += parseInt(item.amount);
					dateArray.push(item.transactionDate);
			  })
			: null;
		uniqueDateArray = [...new Set(dateArray)];
		setTotalPayableAmount(count);
		setAllTransactionDates(uniqueDateArray);
	}, [allTransactionArrayLength]);

	const actionOnNotifyBtnClick = (id, name, e) => {
		if (e.target.tagName == "IMG" || e.target.tagName == "img") {
			e.stopPropagation();
		}

		notifyUserTransaction(id, totalPayableAmount, name, e.target.dataset.phone);
	};
	/*
	const openCurrentUserTransactionHistory = () => {
		props.history.push("/money-given/user/" + id);
	};
	*/
	return (
		<div
			className="user-transaction-thumbnail-main-container  px-2 mt-5 mb-4  py-4"
			style={{
				background: theme[selectedTheme].color,
				boxShadow: theme[selectedTheme].boxShadow,
			}}
			key={id}
			id={id}
		>
			<div className="row d-flex justify-content-center m-0  ">
				<div className="col-xl-2 col-lg-2 col-md-2 p-0 d-flex justify-content-xl-center justify-content-lg-center mb-xl-0 mb-lg-0 mb-2 justify-content-start">
					<img
						src={userImage}
						className="img-fluid  user-transaction-data-thumnail__image"
					/>
				</div>
				<div className="col-xl-6 col-lg-6 col-md-6 d-flex flex-column align-items-start justify-content-between">
					<h1
						className="user-transaction-data-thumnail__name  mb-xl-0 mb-lg-0 mt-xl-0 mt-lg-0 mt-md-0 mb-2 mt-4"
						style={{ color: theme[selectedTheme].textColor }}
					>
						{name.split("").splice(0, 11)}&nbsp;
						<span className="user-transaction-data-thumbnail__phone d-flex my-1 align-items-center">
							<img src={telephone} className="phone-icon mr-1" />
							{phone}
						</span>
					</h1>
					{settlementDate ? (
						<div className="w-100">
							<p className="user-transaction-data-thumnail__amount-summary w-100 my-2 text-left">
								<strong>This transaction got settled on :</strong>
							</p>
							<div className="user-transaction-data-thumbnail__date-container w-100 ">
								<div className="user-transaction-data-thumnail__date mt-1 px-2  py-1 d-flex justify-content-center align-items-center">
									<img
										src={clockIcon}
										className="img-fluid mr-2 user-transaction-data-thumnail__date-clock-icon"
										alt="Clock icon"
									/>
									<h6 className="user-transaction-data-thumnail__date-text  text-white  m-0">
										{settlementDate}
									</h6>
								</div>
							</div>
						</div>
					) : (
						<div className="w-100">
							<p className="user-transaction-data-thumnail__amount-summary w-100 my-2 text-left">
								All the dates when you made transactions with {name}
								<br />
								<strong>Scroll to view all</strong>
								<br />
								<strong>
									Click on any date to view the corresponding transaction.
								</strong>
							</p>
							<div className="user-transaction-data-thumbnail__date-container w-100 ">
								{allTransactionDates.length > 0
									? allTransactionDates.map((item) => {
											return (
												<Link
													to={"/transaction/date/" + id + "/" + item}
													key={Math.random() * 1000 + "-" + item}
													className="user-transaction-data-thumnail__date underline-text mt-1 px-2  py-1 d-flex justify-content-center align-items-center"
												>
													<img
														src={clockIcon}
														className="img-fluid mr-2 user-transaction-data-thumnail__date-clock-icon"
														alt="Clock icon"
													/>
													<h6 className="user-transaction-data-thumnail__date-text text-white  m-0">
														{item.length == 0
															? ""
															: item.split("-").reverse().join("-")}
													</h6>
												</Link>
											);
									  })
									: `No transactions...`}
							</div>
						</div>
					)}

					<div className="user-transaction-data-thumbnail__tagline-row d-flex align-items-center">
						<span className="pl-0 d-flex justify-content-center align-items-center">
							{window.location.hash == "#/settled-transactions" ? (
								<img
									src="https://raw.githubusercontent.com/vaibhavkaul69/todo-money/master/src/images/settled-icon.png"
									alt="Settled icon"
									className="settled-icon"
								/>
							) : (
								<img
									src="https://raw.githubusercontent.com/vaibhavkaul69/todo-money/master/src/images/info.gif"
									alt="Settled icon"
									className="settled-icon"
								/>
							)}
						</span>
						<p
							className="  user-transaction-data-thumnail__tagline  my-3"
							style={{ color: theme[selectedTheme].textColor }}
						>
							{window.location.hash == "#/settled-transactions"
								? "Transaction Settled :)"
								: addTagLineSpecificToURL[window.location.hash]["tagline"]}
						</p>
					</div>
					{/*
					<p
						className="user-transaction-data-thumnail__description"
						style={{
							color:
								selectedTheme == "light" ? theme.dark.color : theme.light.color,
						}}
					>
						I'm a little confused why we keep making class components...
					</p>
					*/}
				</div>
				<div className="col-xl-4 col-lg-4 col-md-4 amount-payable__container d-flex flex-column align-items-xl-center align-items-lg-center align-items-md-center align-items-end">
					<h1
						className=" user-transaction-data-thumnail__amount font-weight-bold text-xl-center text-lg-center text-md-center text-right mt-xl-5 mt-lg-5 mt-2"
						style={{ color: theme[selectedTheme].textColor }}
					>
						Rs. {totalAmount ? totalAmount : totalPayableAmount}
					</h1>
					<p className="user-transaction-data-thumnail__amount-summary mx-xl-auto mx-lg-auto mx-md-auto ml-4   text-center">
						{window.location.hash == "#/settled-transactions"
							? "This is the total settling amount for the current user"
							: addTagLineSpecificToURL[window.location.hash]["amountSummary"]}
					</p>
				</div>
			</div>
			<div className="user-transaction-data-thumnail__action-btns mt-3 w-100">
				<div className="action-btns__CRUD d-flex w-100 justify-content-xl-end justify-content-lg-end justify-content-center">
					{window.location.hash == "#/settled-transactions" ? null : (
						<button
							id={id}
							name={name}
							data-phone={phone}
							onClick={(e) => actionOnNotifyBtnClick(e.target.id, name, e)}
							className="action-btn__notify text-white mx-2 px-2 d-flex justify-content-center align-items-center transaction-thumbnail-action-btn"
						>
							Notify
							{notifyUserCount == 0 ? (
								<img
									id={id}
									name={name}
									data-phone={phone}
									onClick={(e) => actionOnNotifyBtnClick(e.target.id, name, e)}
									className="whatsapp-icon ml-1"
									src="https://raw.githubusercontent.com/vaibhavkaul69/todo-money/f1f410173e2f3d23175473d552598fc434ab8ad5/src/images/whatsapp.svg"
								/>
							) : (
								" (" + notifyUserCount + ")"
							)}
						</button>
					)}
					{window.location.hash == "#/settled-transactions" ? null : (
						<button
							id={id}
							name={totalPayableAmount}
							data-name={name}
							onClick={(e) =>
								settleCurrentTransaction(
									e.target.id,
									totalPayableAmount,
									e.target.dataset.name
								)
							}
							className="action-btn__settle mr-2 px-2 transaction-thumbnail-action-btn"
						>
							Settle
						</button>
					)}

					<button
						id={id}
						onClick={(e) =>
							deleteCurrentTransactionThread(e.target.id, window.location.hash)
						}
						className="action-btn__delete  px-2 mr-2 transaction-thumbnail-action-btn"
					>
						Delete
					</button>
					{window.location.hash == "#/settled-transactions" ? null : (
						<Link
							id={id}
							to={"/money-given/user/" + id}
							className="action-btn__history d-flex justify-content-center align-items-center  text-white px-2 transaction-thumbnail-action-btn"
						>
							Load History
						</Link>
					)}
				</div>
			</div>
		</div>
	);
}

export default withRouter(UserTransactionDataThumbnail_Main);



