import React, { useContext } from "react";
//import { AppThemeContext } from "../../../Contexts/AppThemeContext";
//import { UserAuthContext } from "../../../Contexts/UserAuthContext";
import { UserTransactionDataContext } from "../../../Contexts/UserTransactionDataContext";

function AddUserTransactionBtn(props) {
	/*
	const { selectedTheme } = useContext(AppThemeContext);
	const { isUserLoggedIn } = useContext(UserAuthContext);
	*/
	const { clearUserTransactionDataState } = useContext(
		UserTransactionDataContext
	);

	function actionOnPlusButtonClick() {
		clearUserTransactionDataState();
		props.toggleModalContainerVisibility(true);
	}

	return (
		<button
			onClick={actionOnPlusButtonClick}
			style={{ borderRadius: "10px" }}
			className="d-flex justify-content-center  px-xl-4 px-lg-4 px-md-3 px-2  font-weight-light text-white align-items-center open-add-transaction-modal-btn"
		>
			<img
				src="https://raw.githubusercontent.com/vaibhavkaul69/todo-money/56bacddd99106afb698d51a0768bfbe9b73e3b69/src/images/add_icon.svg"
				className="add-user-transaction-icon mr-2"
				alt="Add icon"
			/>
			<span>ADD</span>

			{/*
						<img
							src={UserAddIcon}
							alt="Add User Icon"
							className="img-fluid add-user-icon ml-2"
						/>
			*/}
		</button>
	);
}

export default AddUserTransactionBtn;



