import React, { createContext, Component } from "react";
import { UserAuthContext } from "./UserAuthContext";
//Create a context.
export const UserTransactionDataContext = createContext();

class UserTransactions {
	constructor(name) {
		this.id = (Math.random() * 10000).toFixed(5).replace(".", "");
		this.name = name;
	}
}
class UserTransaction__Main extends UserTransactions {
	constructor(name, phone) {
		super(name);
		(this.transactionHistory = []), (this.notifyUserCount = 1);
		this.phone = phone;
		this.totalAmount = 0;
	}
}
class UserTransaction_Main_HistoryThread extends UserTransactions {
	constructor(name, amount, date, description, parentId) {
		super(name);
		this.parentId = parentId;
		this.amount = amount;
		this.transactionDate = date;
		this.description = description;
	}
}
class UserTransactionDataContextProvider extends Component {
	static contextType = UserAuthContext;
	constructor(props) {
		super(props);
		this.state = {
			phone: "",
			name: "",
			description: "",
			//dateofUserAdded: "",
			dateOfTransaction: "",
			amount: 0,
			userTransactionsInfo: [],
			settledTransactions: [],
			notify: false,
			uniqueImportantTransactions: [],
			uniqueArrayChanged: 0,
			allTransactionArrayLength: 0,
		};
		(this.importantTransactions = []), (this.parentId = null);
		(this.userAllTransactionHistory = []),
			(this.UserAddHistory = ""),
			(this.intermediateArray = []),
			(this.intermediateUniqueArray = []),
			(this.doesInputErrorExist = false),
			(this.addDataToUserInfo = this.addDataToUserInfo.bind(this));
	}
	searchUserByName = (name, routeHash) => {
		//console.log(name, routeHash);
		let lowerName = name.toLowerCase();
		if (routeHash == "#/money-given") {
			//console.log(this.state.userTransactionsInfo);
			const selectedUser = this.state.userTransactionsInfo.filter((item) => {
				if (item.name.toLowerCase().indexOf(lowerName) != -1) {
					return name;
				}
			});
			//console.log(selectedUser);
			if (selectedUser.length == 0) {
				alert("User not found.!");
				window.location.reload();
			} else {
				this.setState({
					userTransactionsInfo: selectedUser,
				});
			}
		} else if (routeHash == "#/settled-transactions") {
			//console.log(this.state.settledTransactions);
			const selectedUser = this.state.settledTransactions.filter((item) => {
				if (item.name.toLowerCase().indexOf(lowerName) != -1) {
					return name;
				}
			});
			//console.log(selectedUser);
			if (selectedUser.length == 0) {
				alert("User not found.!");
				window.location.reload();
			} else {
				this.setState({
					settledTransactions: selectedUser,
				});
			}
		} else {
			const selectedUser = this.state.uniqueImportantTransactions.filter(
				(item) => {
					if (item.name.toLowerCase().indexOf(lowerName) != -1) {
						return name;
					}
				}
			);
			//console.log(selectedUser);
			if (selectedUser.length == 0) {
				alert("User not found.!");
				window.location.reload();
			} else {
				this.setState({
					uniqueImportantTransactions: selectedUser,
				});
			}
		}
	};
	notifyUserTransaction = (id, totalPayAmount, name, phone) => {
		//console.log(name, phone);
		const filterCurrentUserWithId = this.state.userTransactionsInfo.filter(
			(item) => {
				if (item.id == id) {
					return item;
				}
			}
		);
		//console.log(filterCurrentUserWithId);
		const apiKey =
			"UfizlaTIvWLb81m7dN3RJpejyFg6HDPYsXVrEtcw5nZGoBuA0QCSl2VFMuqbYRtWvaJ19PxBNiHcz6OI";
		const selectedUser = filterCurrentUserWithId[0];
		//const receiverName = selectedUser.name.toUpperCase();
		const senderName = this.context.name.toUpperCase();
		const link = "https://todomoney.netlify.app";
		const message = `\nHi. ${name.toUpperCase()},\n You have taken a total of Rs. ${totalPayAmount} from ${senderName}. Kindly settle this as early as possible.\nThank You :)\n Try : ${link} today.`;
		const encodedMessage = encodeURI(message);
		const userPhone = phone;
		const whatsappAPI = `https://wa.me/91${userPhone}?text=${encodedMessage}`;

		if (window.navigator.onLine) {
			if (selectedUser.notifyUserCount < 1) {
				window.open(whatsappAPI, "_blank");
			} else {
				alert("Sending text notification....");
				fetch(
					`https://www.fast2sms.com/dev/bulk?authorization=${apiKey}&sender_id=TodoMoney&message=${message}&language=english&route=p&numbers=${userPhone}`
				)
					.then((res) => {
						let i = 0;
						//console.log(res);
						if (res.status == 200) {
							for (i = 0; i < this.state.userTransactionsInfo.length; i++) {
								if (this.state.userTransactionsInfo[i].id == id) {
									this.state.userTransactionsInfo[i].notifyUserCount--;
									this.setState({
										notify: true,
									});
									break;
								}
							}
							alert("Text message has been sent :)");

							//console.log(this.state.userTransactionsInfo[i].notifyUserCount);
							setTimeout(() => {
								this.storeUserTransactionInfoToStorage();
							}, 0);
							return res.json();
						} else {
							return res.json();
							/*
							alert(
								"Messages could not be sent.! \nEither it is a mistake at our end or you have written an Invalid Number \nPlease try again :("
							);
							*/
						}
					})
					.then((jsonRes) => {
						if (jsonRes.status_code == 415 || jsonRes.status_code == 416) {
							alert(
								"Message could not be sent due to some technical issue :(\n Meanwhile you can still send unlimited Whatsapp Notifications :)" //Balance over and account blocked
							);
							let i = 0;
							for (i = 0; i < this.state.userTransactionsInfo.length; i++) {
								if (this.state.userTransactionsInfo[i].id == id) {
									this.state.userTransactionsInfo[i].notifyUserCount--;
									this.setState({
										notify: true,
									});
									break;
								}
							}
							setTimeout(() => {
								this.storeUserTransactionInfoToStorage();
							}, 0);
						} else if (jsonRes.status_code == 411) {
							alert("Message could not be sent.!\nReason : " + jsonRes.message); //Invalid number
						}
					})
					.catch((err) => {
						console.log(err);
						alert(
							"Messages could not be sent.!\n Either an error has occured or you are offline.\nPlease try again :("
						);
					});
			}
		} else {
			alert(
				"You are offline.!\n SMS notification can only be sent when you comeback online."
			);
		}
	};

	//Only for deleting history threads of main transactions
	deleteHistoryThread = (user, id) => {
		//console.log(user);
		for (let i = 0; i < user.transactionHistory.length; i++) {
			if (user.transactionHistory[i].id == id) {
				user.transactionHistory.splice(i, 1);
			}
		}
		for (let j = 0; j < this.userAllTransactionHistory.length; j++) {
			if (this.userAllTransactionHistory[j].id == id) {
				this.userAllTransactionHistory.splice(j, 1);
			}
		}
		//console.log(this.userAllTransactionHistory);
		const newUserTransactionInfo = this.state.userTransactionsInfo;
		this.setState({
			userTransactionsInfo: newUserTransactionInfo,
		});
		window.location.reload();
		//console.log("state array : ", this.state.userTransactionsInfo);
	};

	deleteMainTransactionThumbnail = (newUserTransactionInfo, id) => {
		for (let i = 0; i < newUserTransactionInfo.length; i++) {
			if (newUserTransactionInfo[i].id == id) {
				newUserTransactionInfo.splice(i, 1);
			}
		}

		if (newUserTransactionInfo.length == 0) {
			this.userAllTransactionHistory = [];
			this.setState({
				uniqueImportantTransactions: [],
			});
			this.settledTransactions = [];
		}
		this.setState({
			userTransactionsInfo: newUserTransactionInfo,
		});
	};

	deleteCurrentTransactionThread = (id, route) => {
		//console.log(id, route);
		const newUserTransactionInfo = this.state.userTransactionsInfo;

		if (route == "#/money-given") {
			this.deleteMainTransactionThumbnail(newUserTransactionInfo, id);
		} else if (route == "#/important-transactions") {
			const impTransactions = JSON.parse(
				localStorage.getItem("ImportantTransactions")
			);
			//console.log(impTransactions);
			for (let i = 0; i < impTransactions.length; i++) {
				if (impTransactions[i].id == id) {
					impTransactions[i].isImportant = false;
					impTransactions.splice(i, 1);
				}
			}
			for (let i = 0; i < this.state.userTransactionsInfo.length; i++) {
				this.state.userTransactionsInfo[i].transactionHistory.map((item) => {
					if (item.id == id) {
						//console.log(item);
						item.isImportant = false;
					}
				});
			}
			//console.log(impTransactions);
			this.setState({
				uniqueImportantTransactions: impTransactions,
				userTransactionsInfo: this.state.userTransactionsInfo,
			});
			setTimeout(() => {
				this.storeUserTransactionInfoToStorage();
			}, 0);

			//Store data to storage

			/*
			setTimeout(() => {
				this.storeUserTransactionInfoToStorage();
				alert("Stored");
			}, 0);
			window.location.reload();
			console.log(this.uniqueImportantTransactions);

			//window.location.reload();
			*/
		} else if (route == "#/settled-transactions") {
			const newSettledTransactions = JSON.parse(
				localStorage.getItem("SettledTransactions")
			);
			//console.log(newSettledTransactions);
			for (let i = 0; i < newSettledTransactions.length; i++) {
				if (newSettledTransactions[i].id == id) {
					newSettledTransactions.splice(i, 1);
				}
			}
			//console.log(newSettledTransactions);
			this.setState({
				settledTransactions: newSettledTransactions,
			});

			window.location.reload();
			setTimeout(() => {
				this.storeUserTransactionInfoToStorage();
			}, 0);
		} else {
			const hashRouteId = route.split("/")[3];
			//console.log(newUserTransactionInfo);
			const userBasedOnHashId = newUserTransactionInfo.filter((item) => {
				if (item.id == hashRouteId) {
					console.log(item);
					return item;
				}
			});
			//console.log(userBasedOnHashId);
			this.deleteHistoryThread(userBasedOnHashId[0], id);
		}

		//Store data to storage
		setTimeout(() => {
			this.storeUserTransactionInfoToStorage();
		}, 0);
	};

	settleCurrentTransaction = (id, amount, userName) => {
		//console.log(id);
		const settleCurrent = this.state.userTransactionsInfo.filter((item) => {
			if (item.id == id) {
				return item;
			}
		});
		const settleItemWithAmount = settleCurrent[0];
		settleItemWithAmount["totalAmount"] = amount;
		//console.log(settleItemWithAmount);
		this.intermediateArray.push(settleItemWithAmount);
		//console.log(settleCurrent);
		//console.log(this.intermediateArray);
		this.setState({
			settledTransactions: this.intermediateArray,
		});
		this.deleteMainTransactionThumbnail(this.state.userTransactionsInfo, id);
		alert(userName + "'s Transactions settled successfully.!");
		setTimeout(() => {
			this.storeUserTransactionInfoToStorage();
		}, 0);
	};

	markTransactionImportant = (id) => {
		//console.log(id);
		const filterCurrentTransaction = this.userAllTransactionHistory.filter(
			(item) => {
				if (item.id == id) {
					return item;
				}
			}
		);
		//console.log(filterCurrentTransaction);

		const currentImpMarkedTransaction = filterCurrentTransaction[0];
		currentImpMarkedTransaction.isImportant = true;

		//console.log(filterCurrentTransaction[0]);

		for (let i = 0; i < this.state.userTransactionsInfo.length; i++) {
			this.state.userTransactionsInfo[i].transactionHistory.map((item) => {
				if (item.id == id) {
					//console.log(item);
					item.isImportant = true;
				}
			});
		}

		if (this.state.uniqueImportantTransactions.length == 0) {
			//console.log("Length zero hai");
			this.intermediateUniqueArray.push(currentImpMarkedTransaction);
			this.setState({
				uniqueImportantTransactions: this.intermediateUniqueArray,
			});
			//Store data to storage
			setTimeout(() => {
				this.storeUserTransactionInfoToStorage();
			}, 0);
			return true;
		} else {
			const duplicateImpTransaction = this.state.uniqueImportantTransactions.filter(
				(item) => {
					if (item.id == id) {
						return item;
					}
				}
			);

			if (duplicateImpTransaction.length > 0) {
				alert("Transaction Already Marked Important.!");
				return false;
			} else {
				this.intermediateUniqueArray.push(currentImpMarkedTransaction);
				//console.log(this.intermediateUniqueArray);
				this.setState({
					uniqueImportantTransactions: this.intermediateUniqueArray,
				});
				//console.log(this.state.uniqueImportantTransactions);
				//Store data to storage
				setTimeout(() => {
					this.storeUserTransactionInfoToStorage();
				}, 0);
				return true;
			}
		}

		//Here we add the selected important transaction to the ImportantTransactions array.
	};

	storeUserTransactionInfoToStorage = () => {
		localStorage.setItem(
			"UserTransactionDataStore",
			JSON.stringify(this.state.userTransactionsInfo)
		);
		localStorage.setItem(
			"UserAllTransactionsHistory",
			JSON.stringify(this.userAllTransactionHistory)
		);
		localStorage.setItem(
			"ImportantTransactions",
			JSON.stringify(this.state.uniqueImportantTransactions)
		);
		localStorage.setItem(
			"SettledTransactions",
			JSON.stringify(this.state.settledTransactions)
		);
	};
	addDataToUserInfo(formBodyName) {
		if (formBodyName == "addUser") {
			if (
				this.state.name == "" ||
				!/[a-zA-Z0-9]+/.test(this.state.name) ||
				this.state.phone.length < 10 ||
				this.state.phone.length > 10
				/*
				this.state.phone.charAt(0) !== 7 ||
				this.state.phone.charAt(0) !== 8 ||
				this.state.phone.charAt(0) !== 9
				*/
			) {
				alert("You need to enter a valid value.!");
				return false;
			} else {
				const duplicateUser = this.state.userTransactionsInfo.filter((item) => {
					if (item.name == this.state.name) {
						return item;
					}
				});
				//console.log(this.state.userTransactionsInfo);
				//console.log(duplicateUser);
				if (duplicateUser.length > 0) {
					alert(
						`User with same name already exists\nTry a different name like :\n\n${
							this.state.name
						}-${(Math.random() * 100).toFixed(0)} | ${this.state.name}-${(
							Math.random() * 100
						).toFixed(0)} | ${this.state.name}-${(Math.random() * 100).toFixed(
							0
						)}`
					);
					return false;
				} else {
					this.setState({
						userTransactionsInfo: [
							...this.state.userTransactionsInfo,
							new UserTransaction__Main(this.state.name, this.state.phone),
						],
					});
					/*
					console.log(
						new UserTransaction__Main(this.state.name, this.state.phone)
					);
					*/
					setTimeout(() => {
						this.storeUserTransactionInfoToStorage();
					}, 0);
					return true;
				}
			}
		} else {
			if (
				this.state.name == "" ||
				!/[a-zA-Z0-9]+/.test(this.state.name) ||
				this.state.amount == "" ||
				!/[a-zA-Z0-9]+/.test(this.state.amount) ||
				this.state.description == "" ||
				!/[a-zA-Z0-9]+/.test(this.state.description) ||
				this.state.dateOfTransaction == "" ||
				!/[a-zA-Z0-9]+/.test(this.state.dateOfTransaction)
			) {
				alert("You cannot leave the fields empty.!");
				return false;
			} else {
				const userData = this.state.userTransactionsInfo;
				//console.log(this.UserAddHistory);
				const [activeTransactionHistoryUser] = userData.filter(
					(item) => item.name == this.UserAddHistory
				);
				//console.log(activeTransactionHistoryUser);
				this.parentId = activeTransactionHistoryUser.id;
				const num = activeTransactionHistoryUser.transactionHistory.push(
					new UserTransaction_Main_HistoryThread(
						this.UserAddHistory,
						this.state.amount,
						this.state.dateOfTransaction,
						this.state.description,
						this.parentId
					)
				);

				//console.log(activeTransactionHistoryUser);

				//Add all the transaction history into the userAllTransactionHistory
				this.userAllTransactionHistory.push(
					activeTransactionHistoryUser.transactionHistory[num - 1]
				);
				this.setState({
					allTransactionArrayLength: this.userAllTransactionHistory.length,
				});
				/*
				activeTransactionHistoryUser.transactionHistory.map((item) => {
					this.userAllTransactionHistory.push(item);
				});
				*
				console.log(
					this.userAllTransactionHistory,
					this.state.allTransactionArrayLength
				);
				*/

				//Call store to localstorage method
				setTimeout(() => {
					this.storeUserTransactionInfoToStorage();
				}, 0);
				return true;
			}
		}
	}
	setPhone = (e) => {
		this.setState({
			phone: e.target.value,
		});
	};
	setName = (e) => {
		this.setState({
			name: e.target.value,
		});
	};

	setDateOfTransaction = (e) => {
		this.setState({
			dateOfTransaction: e.target.value,
		});
	};
	setAmount = (e) => {
		this.setState({
			amount: e.target.value,
		});
	};
	setDescription = (e) => {
		this.setState({
			description: e.target.value,
		});
	};
	changeUserAddHistory = (activeUser) => {
		this.UserAddHistory = activeUser;
		this.setState({
			name: this.UserAddHistory,
		});
	};
	componentDidMount() {
		//console.log(this.importantTransactions);
		const UserTransactionDataStore = JSON.parse(
			localStorage.getItem("UserTransactionDataStore")
		);
		const UserAllTransactionsHistory = JSON.parse(
			localStorage.getItem("UserAllTransactionsHistory")
		);
		const ImportantTransactions = JSON.parse(
			localStorage.getItem("ImportantTransactions")
		);
		const SettledTransactions = JSON.parse(
			localStorage.getItem("SettledTransactions")
		);
		//console.log(UserTransactionDataStore);
		if (
			UserTransactionDataStore == null &&
			UserAllTransactionsHistory == null &&
			ImportantTransactions == null &&
			SettledTransactions == null
		) {
			return;
		} else {
			//console.log(UserTransactionDataStore, UserAllTransactionsHistory);
			this.setState({
				userTransactionsInfo: UserTransactionDataStore,
			});
			this.setState({
				settledTransactions: SettledTransactions,
			});
			this.setState({
				uniqueImportantTransactions: ImportantTransactions,
			});
			this.userAllTransactionHistory = UserAllTransactionsHistory;
			this.intermediateUniqueArray = ImportantTransactions;
		}
	}
	clearUserTransactionDataState = () => {
		this.setState({
			name: "",
			amount: "",
			description: "",
			dateOfTransaction: "",
			phone: "",
		});
	};
	render() {
		return (
			<UserTransactionDataContext.Provider
				value={{
					...this.state,
					addDataToUserInfo: this.addDataToUserInfo,
					setName: this.setName,
					setDateOfTransaction: this.setDateOfTransaction,
					setAmount: this.setAmount,
					setDescription: this.setDescription,
					setPhone: this.setPhone,
					changeUserAddHistory: this.changeUserAddHistory,
					userAllTransactionHistory: this.userAllTransactionHistory,
					markTransactionImportant: this.markTransactionImportant,
					uniqueImportantTransactions: this.state.uniqueImportantTransactions,
					clearUserTransactionDataState: this.clearUserTransactionDataState,
					notifyUserTransaction: this.notifyUserTransaction,
					parentId: this.parentId,
					settleCurrentTransaction: this.settleCurrentTransaction,
					uniqueArrayChanged: this.uniqueArrayChanged,
					deleteCurrentTransactionThread: this.deleteCurrentTransactionThread,
					searchUserByName: this.searchUserByName,
				}}
			>
				{this.props.children}
			</UserTransactionDataContext.Provider>
		);
	}
}

export default UserTransactionDataContextProvider;



