import React, { useContext } from "react";
import { withRouter } from "react-router";
import { AppThemeContext } from "../Contexts/AppThemeContext";
import Footer from "./Footer/Footer";
//Css in App.css
import UserTransactionDataThumbnail_History_Thread from "./UserTransactionDataThumbnailHistoryThread/UserTransactionDataThumbnail_History_Thread";

function DateBasedSingleTransaction() {
	const { selectedTheme } = useContext(AppThemeContext);
	const id = window.location.hash.split("/")[3];
	const date = window.location.hash.split("/")[4];
	const userAllTransactionHistory = JSON.parse(
		localStorage.getItem("UserAllTransactionsHistory")
	);
	const allUserWithCurrentDate = userAllTransactionHistory.filter((item) => {
		if (item.transactionDate == date && item.parentId == id) {
			return item;
		}
	});
	//console.log(allUserWithCurrentDate, date, id);
	return (
		<React.Fragment>
			<section className="main-bg-container">
				<div className="single-transaction-container">
					<div className="container">
						<h1
							className="history-thread-main-heading my-3 text-center"
							style={{ color: selectedTheme == "light" ? "#1a1a1a" : "#fff" }}
						>
							List of all the transactions that were made on : <br />
							<strong className="font-weight-bold">
								{date.split("-").reverse().join("-")}
							</strong>
							&nbsp; with{" "}
							<strong className="font-weight-bold">
								{allUserWithCurrentDate[0].name}
							</strong>
						</h1>
						{allUserWithCurrentDate.map((item) => {
							return (
								<UserTransactionDataThumbnail_History_Thread
									name={item.name}
									key={Math.random() * 1000}
									id={item.id}
									amount={item.amount}
									transactionDate={item.transactionDate}
									description={item.description}
								/>
							);
						})}
					</div>
				</div>
			</section>
			<Footer />
		</React.Fragment>
	);
}
export default withRouter(DateBasedSingleTransaction);



