import React, { Component, createContext } from "react";
import { ErrorSuccessInfoContext } from "./ErrorSuccessInfoModalContext";
export const UserAuthContext = createContext();

class UserAuthProvider extends Component {
	static contextType = ErrorSuccessInfoContext;
	constructor(props) {
		super(props);
		this.state = {
			name: "",
			password: "",
			email: "",
			isUserLoggedIn: false,
		};
	}
	componentDidMount() {
		const userStorageData = JSON.parse(localStorage.getItem("User"));
		if (userStorageData && userStorageData.isUserLoggedIn) {
			this.setState({
				name: userStorageData.name,
				password: userStorageData.password,
				isUserLoggedIn: true,
			});
			/*
			setTimeout(() => {
				//console.log(this.state);
			}, 1000);
			*/
		}
	}
	checkRegisterUserInput = () => {
		const emailRegex = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
		const lowerEmail = this.state.email.toLowerCase();
		//console.log(emailRegex.test(lowerEmail), lowerEmail);
		if (
			this.state.name == "" ||
			!/[a-zA-Z0-9]+/.test(this.state.name) ||
			this.state.email == "" ||
			this.state.password == "" ||
			!/[a-zA-Z0-9]+/.test(this.state.password)
		) {
			alert("You need to enter a valid value.!");
			return false;
		} else {
			if (emailRegex.test(lowerEmail)) {
				return true;
			} else {
				alert("You need to enter a valid value.!");
				return false;
			}
		}
	};
	storeUserInfo = () => {
		//Create a user in LocalStorage
		localStorage.setItem("User", JSON.stringify(this.state));
		this.context.changeErrorSuccessInfoModalState();
		this.setState({
			name: "",
			password: "",
			email: "",
		});
	};
	setUserAuthName = (userName) => {
		this.setState({
			name: userName,
		});
	};
	setUserAuthPassword = (userPass) => {
		this.setState({
			password: userPass,
		});
	};
	setAuthEmail = (userMail) => {
		this.setState({
			email: userMail,
		});
	};
	logoutUser = () => {
		this.setState({
			isUserLoggedIn: false,
		});
		setTimeout(() => {
			localStorage.setItem("User", JSON.stringify(this.state));
		}, 1000);
	};
	changeUserAuthLoginStatus = () => {
		const user = JSON.parse(localStorage.getItem("User"));
		const userEmail = user.email;
		if (
			user &&
			user.name == this.state.name &&
			user.password == this.state.password
		) {
			this.setState({
				isUserLoggedIn: true,
				email: userEmail,
			});
			setTimeout(() => {
				localStorage.setItem("User", JSON.stringify(this.state));
			}, 1000);

			return true;
		} else {
			this.context.changeErrorSuccessInfoModalState();
			return false;
		}
	};
	render() {
		return (
			<UserAuthContext.Provider
				value={{
					...this.state,
					storeUserInfo: this.storeUserInfo,
					setAuthEmail: this.setAuthEmail,
					setUserAuthName: this.setUserAuthName,
					setUserAuthPassword: this.setUserAuthPassword,
					changeUserAuthLoginStatus: this.changeUserAuthLoginStatus,
					logoutUser: this.logoutUser,
					checkRegisterUserInput: this.checkRegisterUserInput,
				}}
			>
				{this.props.children}
			</UserAuthContext.Provider>
		);
	}
}

export default UserAuthProvider;



