import React, { useContext } from "react";
import "./VideoTutorial.css";
import videoTutorialIcon from "../../images/video-tutorial.svg";
import { ErrorSuccessInfoContext } from "../../Contexts/ErrorSuccessInfoModalContext";
import { Link } from "react-router-dom";
import { ToggleSideNavbar } from "../../Contexts/ToggleSideNavbarContext";

function VideoTutorial() {
	const { changeFeedbackFormModalState } = useContext(ErrorSuccessInfoContext);
	const { toggleSideNavbar } = useContext(ToggleSideNavbar);
	return (
		<Link
			to="/feedback"
			onClick={() => toggleSideNavbar(false)}
			className="side-navbar-video-tutorial pb-lg-5 pb-md-5 pb-4 border-bottom mt-lg-5 mt-md-5 mt-4 px-3 d-flex justify-content-center align-items-center"
		>
			<img
				src={videoTutorialIcon}
				className="video-tutorial-icon mr-3"
				alt="Video Tuorial "
			/>
			<div
				onClick={changeFeedbackFormModalState}
				className="video-tutorial-content"
			>
				<h3 className="video-tutorial-label-text text-left mb-1">
					Feedback Form
				</h3>
				<p className="tutorial-text mb-0 text-left ">
					Please give your valuable feedback
				</p>
			</div>
		</Link>
	);
}

export default VideoTutorial;



